<template>
  <div class="profile-page">
    <!-- Page Header -->
    <div class="page-header bg-white shadow-sm border-b border-gray-200 mb-6">
      <div class="px-6 py-4">
        <div class="flex items-center justify-between">
          <div>
            <h1 class="text-2xl font-bold text-gray-900">Profil Pengguna</h1>
            <p class="mt-1 text-sm text-gray-600">Kelola informasi profil dan pengaturan akun Anda</p>
          </div>
          <button 
            @click="editMode = !editMode"
            class="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
          >
            <Icon :icon="editMode ? 'mdi:close' : 'mdi:pencil'" class="mr-2 h-4 w-4" />
            {{ editMode ? 'Batal' : 'Edit Profil' }}
          </button>
        </div>
      </div>
    </div>

    <!-- Profile Content -->
    <div class="px-6 max-w-4xl">
      <div class="grid grid-cols-1 lg:grid-cols-3 gap-6">
        
        <!-- Profile Avatar & Basic Info -->
        <div class="lg:col-span-1">
          <div class="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
            <div class="text-center">
              <!-- Avatar -->
              <div class="mx-auto w-32 h-32 bg-gradient-to-br from-blue-500 to-purple-600 rounded-full flex items-center justify-center mb-4">
                <span class="text-white font-bold text-4xl">{{ userInitials }}</span>
              </div>
              
              <!-- Basic Info -->
              <h2 class="text-xl font-bold text-gray-900 mb-2">{{ fullName }}</h2>
              <p class="text-gray-600 mb-1">{{ userProfile.email }}</p>
              <p class="text-sm text-gray-500 mb-4">@{{ userProfile.username }}</p>
              
              <!-- Status Badge -->
              <div class="flex justify-center mb-4">
                <span :class="statusBadgeClass" class="inline-flex items-center px-3 py-1 rounded-full text-sm font-medium">
                  <Icon :icon="userProfile.isEnabled ? 'mdi:check-circle' : 'mdi:close-circle'" class="mr-1 h-4 w-4" />
                  {{ userProfile.isEnabled ? 'Aktif' : 'Nonaktif' }}
                </span>
              </div>
              
              <!-- Roles -->
              <div class="flex flex-wrap justify-center gap-2">
                <span 
                  v-for="role in userProfile.roles" 
                  :key="role"
                  class="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-blue-100 text-blue-800"
                >
                  {{ getRoleLabel(role) }}
                </span>
              </div>
            </div>
          </div>
        </div>

        <!-- Profile Details -->
        <div class="lg:col-span-2">
          <div class="bg-white rounded-lg shadow-sm border border-gray-200">
            
            <!-- Profile Form -->
            <div class="p-6">
              <h3 class="text-lg font-medium text-gray-900 mb-6">Informasi Profil</h3>
              
              <form @submit.prevent="saveProfile" class="space-y-6">
                <!-- Personal Information -->
                <div class="grid grid-cols-1 md:grid-cols-2 gap-6">
                  <div>
                    <label class="block text-sm font-medium text-gray-700 mb-2">Nama Depan</label>
                    <el-input
                      v-model="profileForm.firstName"
                      :disabled="!editMode"
                      placeholder="Nama depan"
                      size="large"
                    />
                  </div>
                  
                  <div>
                    <label class="block text-sm font-medium text-gray-700 mb-2">Nama Belakang</label>
                    <el-input
                      v-model="profileForm.lastName"
                      :disabled="!editMode"
                      placeholder="Nama belakang"
                      size="large"
                    />
                  </div>
                </div>
                
                <div>
                  <label class="block text-sm font-medium text-gray-700 mb-2">Username</label>
                  <el-input
                    v-model="profileForm.username"
                    disabled
                    placeholder="Username"
                    size="large"
                  >
                    <template slot="prepend">@</template>
                  </el-input>
                  <p class="mt-1 text-sm text-gray-500">Username tidak dapat diubah</p>
                </div>
                
                <div>
                  <label class="block text-sm font-medium text-gray-700 mb-2">Email</label>
                  <el-input
                    v-model="profileForm.email"
                    :disabled="!editMode"
                    type="email"
                    placeholder="Email"
                    size="large"
                  />
                </div>
                
                <!-- Account Information -->
                <div class="border-t border-gray-200 pt-6">
                  <h4 class="text-md font-medium text-gray-900 mb-4">Informasi Akun</h4>
                  
                  <div class="grid grid-cols-1 md:grid-cols-2 gap-6">
                    <div>
                      <label class="block text-sm font-medium text-gray-700 mb-2">Tanggal Bergabung</label>
                      <el-input
                        :value="formatDate(userProfile.createdAt)"
                        disabled
                        size="large"
                      />
                    </div>
                    
                    <div>
                      <label class="block text-sm font-medium text-gray-700 mb-2">Login Terakhir</label>
                      <el-input
                        :value="formatDateTime(userProfile.lastLogin)"
                        disabled
                        size="large"
                      />
                    </div>
                  </div>
                </div>
                
                <!-- Save Button -->
                <div v-if="editMode" class="flex justify-end pt-6 border-t border-gray-200">
                  <div class="flex space-x-3">
                    <el-button @click="cancelEdit" size="large">
                      Batal
                    </el-button>
                    <el-button 
                      type="primary" 
                      :loading="loading"
                      native-type="submit"
                      size="large"
                    >
                      <Icon icon="mdi:content-save" class="mr-2 h-4 w-4" />
                      {{ loading ? 'Menyimpan...' : 'Simpan Perubahan' }}
                    </el-button>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Icon } from "@iconify/vue2";
import { mapGetters, mapActions, mapState } from 'vuex';

export default {
  name: 'Profile',
  components: {
    Icon
  },
  data() {
    return {
      editMode: false,
      profileForm: {
        firstName: '',
        lastName: '',
        username: '',
        email: ''
      }
    }
  },
  computed: {
    ...mapGetters(['user']),
    ...mapState(['loading']),
    
    userProfile() {
      return this.user || {
        id: '',
        username: '',
        email: '',
        firstName: '',
        lastName: '',
        isEnabled: true,
        roles: ['USER'],
        createdAt: new Date().toISOString(),
        lastLogin: new Date().toISOString()
      };
    },
    
    fullName() {
      return `${this.userProfile.firstName} ${this.userProfile.lastName}`.trim() || 'Nama Pengguna';
    },
    
    userInitials() {
      const firstName = this.userProfile.firstName || '';
      const lastName = this.userProfile.lastName || '';
      
      if (firstName && lastName) {
        return (firstName.charAt(0) + lastName.charAt(0)).toUpperCase();
      } else if (firstName) {
        return firstName.substring(0, 2).toUpperCase();
      } else if (this.userProfile.username) {
        return this.userProfile.username.substring(0, 2).toUpperCase();
      }
      
      return 'UN';
    },
    
    statusBadgeClass() {
      return this.userProfile.isEnabled 
        ? 'bg-green-100 text-green-800' 
        : 'bg-red-100 text-red-800';
    }
  },
  methods: {
    ...mapActions(['fetchUserProfile']),
    
    getRoleLabel(role) {
      const roleLabels = {
        'USER': 'Pengguna',
        'ADMIN': 'Administrator',
        'SUPER_ADMIN': 'Super Admin',
        'REPORTER': 'Pelapor'
      };
      return roleLabels[role] || role;
    },
    
    formatDate(dateString) {
      if (!dateString) return '-';
      return new Intl.DateTimeFormat('id-ID', {
        year: 'numeric',
        month: 'long',
        day: 'numeric'
      }).format(new Date(dateString));
    },
    
    formatDateTime(dateString) {
      if (!dateString) return '-';
      return new Intl.DateTimeFormat('id-ID', {
        year: 'numeric',
        month: 'short',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      }).format(new Date(dateString));
    },
    
    initializeForm() {
      this.profileForm = {
        firstName: this.userProfile.firstName || '',
        lastName: this.userProfile.lastName || '',
        username: this.userProfile.username || '',
        email: this.userProfile.email || ''
      };
    },
    
    cancelEdit() {
      this.editMode = false;
      this.initializeForm();
    },
    
    async saveProfile() {
      try {
        // Here you would implement the API call to update profile
        // For now, we'll just show a success message
        this.$message.success('Profil berhasil diperbarui');
        this.editMode = false;
        
        // Refresh profile data
        await this.fetchUserProfile();
      } catch (error) {
        console.error('Error updating profile:', error);
        this.$message.error('Gagal memperbarui profil');
      }
    },
    
    async loadProfile() {
      try {
        await this.fetchUserProfile();
        this.initializeForm();
      } catch (error) {
        console.error('Error loading profile:', error);
        this.$message.error('Gagal memuat data profil');
      }
    }
  },
  
  mounted() {
    this.loadProfile();
  },
  
  watch: {
    user: {
      handler() {
        this.initializeForm();
      },
      immediate: true
    }
  }
}
</script>

